import { Component, OnInit } from '@angular/core';
import { UserService } from '../../user/shared/user.service';
import { Video } from '../../user/shared/video';
import {NgForm} from '@angular/forms';

@Component({
  selector: 'video-center',
  templateUrl: './video-center.component.html',
  styleUrls: ['./video-center.component.css'],
  providers: [UserService]
})
export class VideoCenterComponent implements OnInit {

  videos: Array<Video>;
  selectedVideo: Video;
  private hidenewVideo: boolean = true;

  constructor(private userService: UserService) { }

  ngOnInit() {
    this.userService.getVideos()
    .subscribe((resVideoData: any) => this.videos = resVideoData);
  }
  
  onSelectVideo(video:any){
    this.selectedVideo = video;
    this.hidenewVideo = true;
    console.log(this.selectedVideo);
  }

  onSubmit(form :NgForm){
    this.userService.addVideo(form.value).subscribe((res)=>{
    this.hidenewVideo = true;
    this.selectedVideo = null;
    alert("Data Save Successfully");
    form.reset();
  });
  }

  newVideo(){
    this.hidenewVideo = false;
  }

  onUpdateVideoEvent(video: any){
    this.userService.updateVideo(video)
    .subscribe(resUpdateVideo => video = resUpdateVideo);
    this.selectedVideo = null;
    alert("Data Updeated Successfully");
  }

  onDeleteVideoEvent(video: any){
    let videoArray = this.videos;
    if(confirm('Are you sure to delete this record ?') == true){
    this.userService.deleteVideo(video)
    .subscribe(resDeleteVideo => {
      for(let i=0; i<videoArray.length; i++){
        if(videoArray[i]._id === video._id){
          videoArray.splice(i,1);
        }
      }
    });
    this.selectedVideo = null;
    }
  };
}
