import { Injectable } from '@angular/core';
import { User } from './user.model';
import { Idea } from './idea';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from '../../../environments/environment';
import { Video } from './video';
import { map } from 'rxjs/operators';

@Injectable({
  providedIn: 'root'
})
export class UserService {
  selectedUser: User = {
    fullName: '',
    email: '',
    password: ''
  };

  noAuthHeader = { headers: new HttpHeaders({ 'NoAuth': 'True' }) };
  readonly Url ='http://localhost:3000/api/videos';
  selectedVideo : Video;
  constructor(private http: HttpClient) { }

  //HttpMethods
  postUser(user: User){
    return this.http.post(environment.apiBaseUrl+'/register',user,this.noAuthHeader);
  }
  postIdea(idea: Idea){
    return this.http.post(environment.apiBaseUrl+'/idea',idea);
  }
  login(authCredentials) {
    return this.http.post(environment.apiBaseUrl + '/authenticate', authCredentials,this.noAuthHeader);
  }
  getUserProfile() {
  return this.http.get(environment.apiBaseUrl + '/userProfile');
  }

//Helpers Methods
  setToken(token: string) {
    localStorage.setItem('token', token);
  }

  getToken() {
    return localStorage.getItem('token');
  }

  deleteToken() {
    localStorage.removeItem('token');
  }
  
  getUserPayload() {
    var token = this.getToken();
    if (token) {
      var userPayload = atob(token.split('.')[1]);
      return JSON.parse(userPayload);
    }
    else
      return null;
  }

  isLoggedIn() {
    var userPayload = this.getUserPayload();
    if (userPayload)
      return userPayload.exp > Date.now() / 1000;
    else
      return false;
  }

//strat video upload
  getVideos(){
    return this.http.get(this.Url);
  }

  addVideo(video : Video){
    return this.http.post(this.Url, video);
  }

  updateVideo(video : Video){
    return this.http.put(this.Url + `/${video._id}`,video);
  }

  deleteVideo(video: Video){
    return this.http.delete(this.Url + `/${video._id}`);
  }

}

