import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { UserComponent } from './user/user/user.component';
import { SignUpComponent } from './user/sign-up/sign-up.component';
import { UserProfileComponent } from './user/user-profile/user-profile.component';
import { SignInComponent } from './user/sign-in/sign-in.component';
import { AuthGuard } from './auth/auth.gaurd';
import { ImageComponent } from './user/image/image.component';
import { MenuComponent } from './dashborad/menu/menu.component';
import { HomeComponent } from './dashborad/home/home.component';
import { NewIdeasComponent } from './user/new-ideas/new-ideas.component';
import { VideoCenterComponent } from './video/video-center/video-center.component';

const routes: Routes = [
   {
        path: 'signup', component: UserComponent,
        children: [{ path: '', component: SignUpComponent }]
    },
    {
        path: 'login', component: UserComponent,
        children: [{ path: '', component: SignInComponent }]
    },
    {
       path: 'userProfile', component: UserProfileComponent,canActivate:[AuthGuard]
    },
    {
        path: '', redirectTo: '/login', pathMatch: 'full'
    },
    {
        path: 'image', component: ImageComponent,canActivate:[AuthGuard]
    },
    {
        path: 'menu', component: MenuComponent,canActivate:[AuthGuard]
    },
    {
        path: 'home', component: HomeComponent,canActivate:[AuthGuard]
    },
    {
        path: 'idea', component: NewIdeasComponent,canActivate:[AuthGuard]
    },
    {
        path: 'video-center', component: VideoCenterComponent,canActivate:[AuthGuard]
    }          
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }


